import http from "../http";


export { getOrderListService,getOrderByKeyService,setOrderReceived,getPurchaseListService,getReturnListService,getReturnOrderByKeyService,setReturnStatusService,sendGoodsArriveMsg,getDadaOrderTrack}

//获取订单列表
const getOrderListService = (params) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-commerce-sale/saler/orderList', params, true).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}

