<template>
  <div class="commerceSearch">
    <navigation-bar pageName="订单搜索" right-text @onLeftClick="onClickLeft" />
    <div class="searchPanel">
      <div class="searchInput">
        <img class="CommerceSearchImg" :src="searchIcon" />
        <input type="text" placeholder="请输入订单号" v-model="subBillNo" @keyup.enter="processKey" ref="myInput"/>
        <img class="CommerceSearchImg" @click="cancelSearch" :src="clearIcon" />
      </div>
    </div>
    <div class="commerce-over-box">
      <div v-if="purchaseList.length <=0 && orderList.length <= 0 && returnList.length <= 0" class="noMessagePanel">
        <div class="noMessage">
          <img :src="noList" class="noMessageImg" />
          <p>暂无订单</p>
        </div>
      </div>
    <van-list
      v-if="orderType==0"
      v-model="orderLoading"
      :finished="orderFinished"
      :finished-text="finishedText"
      @load="loadMore"
      :immediate-check="false">
      <div
        class="card van-clearfix commerceOrderList"
        v-for="(orderInfo,index) in orderList"
        :key="index"
      >
        <!-- 店铺名+订单号 -->
        <div class="orderTitlePanel" @click="gotoCommerceDetail(orderInfo)">
          <div class="orderTitleStore">{{orderInfo.shopName}}</div>
          <div class="orderCode">订单号：{{orderInfo.subBillNo}}</div>
          <div v-if="orderInfo.deliveryType==0" class="SendTypeDelivery">
            <div class="sendType">快递配送</div>
          </div>
          <div v-if="orderInfo.deliveryType==1" class="SendTypeDelivery">
            <div class="sendType">销售配送</div>
          </div>
          <div v-if="orderInfo.deliveryType==2" class="SendTypeSelf">
            <div class="sendType">到店自提</div>
          </div>
        </div>
        <div class="van-hairline--top"></div>
        <!-- 商品图片+详情 -->
        <div class="orderInfoPanel" @click="gotoCommerceDetail(orderInfo)">
          <van-image class="orderImg" lazy-load v-bind:src="orderInfo.firstGoodsPicUrl" />
          <div class="orderDetail">
            <div style="display: flex;">
              <div class="orderName userName">{{orderInfo.recName}}</div>
              <div class="orderName" style="margin-left:10px;">{{orderInfo.recPhone}}</div>
              <div class="orderStatus">{{orderInfo.billStatus}}</div>
            </div>
            <div class="orderName">{{orderInfo.recAddr}}</div>
            <div class="orderName" style="color: #9a9a9a">{{orderInfo.createdTime}}</div>
          </div>
        </div>
        <div class="van-hairline--top lineMargin"></div>
        <!-- 其它商品+总价 -->
        <div class="pricePanel" @click="gotoCommerceDetail(orderInfo)">
          <div class="orderGoods">{{orderInfo.goodsInfo[0].goodsName}}</div>
          <div class="orderGoodsNum">共{{orderInfo.goodsInfo.length}}件商品</div>
          <div>
            <span class="orderPrice">
              <span style="font-size: 14px;">￥</span>
              {{orderInfo.realPayTotal}}
            </span>
          </div>
        </div>
        <div class="receivePanel" v-if="orderInfo.billStatus=='待收货'">
          <div class="receiveBtn" @click="received(orderInfo, index)">已送达</div>
          <div class="receiveBtn" v-if="orderInfo.noticeTimes && orderInfo.noticeTimes <= 0" @click="goodArrive(orderInfo)">已接货</div>
          <div class="receiveBtn-disabled" v-else-if="orderInfo.noticeTimes && orderInfo.noticeTimes >= 1">已接货</div>
          <div class="receiveBtn" v-else @click="goodArrive(orderInfo)">已接货</div>
          <div class="receiveBtn" v-if="dadaExpressSurported && orderInfo.expressCompany && (orderInfo.expressCompany.indexOf('达达') != -1)" @click="getDadaExpressInfo(orderInfo)">查看物流</div>
          <!-- <div class="receiveBtn" @click="getDadaExpressInfo(orderInfo)">查看物流</div> -->
        </div>
      </div>
    </van-list>

    <van-list
      v-if="orderType==1"
      v-model="purchaseLoading"
      :finished="purchaseFinished"
      :finished-text="finishedText"
      @load="loadMore"
      :immediate-check="false"
    >
      <div
        class="card commercePurchaseList"
        v-for="(order,index) in purchaseList"
        :key="index"
      >
        <!-- 采购单号+时间 -->
        <div class="orderTitlePanel">
          <span class="orderTitleCode">采购单号:{{order.billNo}}</span>
          <span class="orderTime">{{order.createdTime}}</span>
        </div>
        <div class="van-hairline--top"></div>
        <!-- 采购信息 -->
        <div class="orderInfoPanel">
          <img class="orderImg" :src="imgDefault" />
          <div class="orderDetail">
            <div class="orderName">{{order.recAreaCode}}</div>
            <div class="orderName">供应商：{{order.supplierName}}</div>
            <div class="orderName">供应商电话：{{order.supplierPhone}}</div>
          </div>
        </div>
        <div class="dashLine"></div>
        <!-- 商品列表 -->
        <div class="pricePanel" v-for="(sku,index) in order.skuList" :key="index">
          <table style="width: 100%;">
            <tr>
              <td style="width: 80%;">
                <div class="orderGoods">{{sku.goodsName}}</div>
              </td>
              <td style="width: 20%">
                <div class="orderPrice">￥{{sku.skuPrice}}</div>
              </td>
            </tr>
          </table>
          <table style="width: 100%;">
            <tr>
              <td>
                <div class="orderSpecs">{{sku.attrValueContent}}</div>
              </td>
              <td style="width: 40px;">
                <div class="orderGoodsNum">x{{sku.purchaseCount}}</div>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </van-list>
    <van-list
      v-if="orderType==2"
      v-model="returnLoading"
      :finished="returnFinished"
      :finished-text="finishedText"
      @load="loadMore"
      :immediate-check="false"
    >

      <div class="card commerceReturnList" v-for="(order,index) in returnList" :key="index">
        <!-- 店铺名+订单号 -->
        <div class="orderTitlePanel" @click="gotoReturnDetail(order)">
          <span class="orderTitleStore">{{order.shopName}}</span>
          <div class="orderCodeJudge">待审核</div>
          <div class="orderCodeReturn" v-if="order.billStatus=='待退款'">{{order.billStatus}}</div>
          <div class="orderCodeFinished" v-if="order.billStatus=='已完成'">{{order.billStatus}}</div>
          <div class="orderCodeHasReturn" v-if="order.billStatus=='已驳回'">{{order.billStatus}}</div>
        </div>
        <div class="van-hairline--top"></div>
        <!-- 退货单号 -->
        <div class="orderInfoPanel" @click="gotoReturnDetail(order)">
          <div class="orderInfoItem">
            <span class="orderInfoTitle">退货单号</span>
            <span class="orderInfoDetai">{{order.returnBillNo}}</span>
          </div>
          <div class="orderInfoItem">
            <span class="orderInfoTitle">申请时间</span>
            <span class="orderInfoDetai">{{order.returnTime}}</span>
          </div>
        </div>
        <div class="dashLine"></div>

        <!-- 订单号 -->
        <div class="orderInfoPanel" @click="gotoReturnDetail(order)">
          <div class="orderInfoItem">
            <span class="orderInfoTitle">订单号</span>
            <span class="orderInfoDetai">{{order.subBillNo}}</span>
          </div>
          <div class="orderInfoItem">
            <span class="orderInfoTitle">下单时间</span>
            <span class="orderInfoDetai">{{order.createdTime}}</span>
          </div>
        </div>
        <div class="dashLine"></div>
        <!-- 地址 -->
        <div class="orderInfoPanel autoHeight" @click="gotoReturnDetail(order)">
          <div class="orderInfoItem">
            <span class="orderInfoTitle">联系人</span>
            <span class="orderInfoDetai">
              {{order.returnName}}
              {{order.returnPhone}}
            </span>
          </div>
          <div class="orderInfoItem">
            <div class="orderInfoTitle">取件地址</div>
            <div class="orderInfoDetai">{{order.returnAddr}}</div>
          </div>
        </div>

        <div class="van-hairline--top"></div>

        <!-- 其它商品+总价 -->
        <div class="pricePanel" @click="gotoReturnDetail(order)">
          <span class="orderGoods">{{order.spuName}}</span>
          <span class="orderGoodsNum">共1件商品</span>
          <span class="orderPrice">
            <span style="font-size: 14px;">￥</span>
            {{order.realPaySkuTotal}}
          </span>
        </div>
        <!-- 根据产品经理需求输入，审核列表同步的情况下不允许操作，屏蔽搜索列表待审核项的操作按钮 -->
        <!--<div class="receivePanel" v-if="order.billStatus=='待审核'">
          <div class="receiveBtn" @click="adoptReturn(order, index)">通过</div>
          <div class="rejectBtn" @click="rejectReturn(order, index)">驳回</div>
        </div>-->
      </div>
    </van-list>
    </div>
  </div>
</template>

<script>
import {
  getOrderByKeyService,
  getReturnOrderByKeyService,
  setReturnStatusService,
  getPurchaseListService,
  sendGoodsArriveMsg,
  setOrderReceived,
  getDadaOrderTrack,
} from "../../services/commerceService.js";
import { store, storage } from "../../utils/store";
import log from '../../utils/log';
import NavigationBar from "../title/NavigationBar";
export default {
  name: "commerceSearch",
  components: { NavigationBar },
  data() {
    return {
      // isLoading: false,
      orderType: 0, //搜索订单分类
      orderList: [],
      orderLoading: false,
      orderFinished: false,
      purchaseList: [],
      purchaseLoading: false,
      purchaseFinished: false,
      returnList: [],
      returnLoading: false,
      returnFinished: false,
      finishedText: "没有更多了",
      subBillNo: "",
      imgDefault: require("../../assets/imgs/commerce/back.png"),
      searchIcon: require("../../assets/imgs/commerce/icon_Shape_black.png"),
      clearIcon: require("../../assets/imgs/commerce/icon_closed.png"),
      orderData: { page: 1, row: 10, rows: 10 },
      userInfo: null,
      noList: require("../../assets/imgs/commerce/img_Default@2x.png"),
      dadaExpressSurported: true,
    };
  },
  activated() {
    this.resetData();
    //获取用户数据
    this.userInfo = store.get("userInfo");
    this.userInfo = this.userInfo
      ? this.userInfo
      : storage.getStorage("custNews");
    this.$nextTick(function() {
      this.$refs.myInput.focus();
    });
  },
  methods: {
    resetData() {
      this.orderLoading = false;
      this.orderFinished = true;
      this.purchaseLoading = false;
      this.purchaseFinished = true;
      this.returnLoading = false;
      this.returnFinished = true;
      // 获取搜索类型
      this.orderType = this.$route.query.orderType;
      // 获取搜索关键字
      let searchKey = store.get("searchKey", "");
      if (searchKey) {
        this.subBillNo = searchKey;
        store.dele("searchKey");
        if (this.orderType == 0) {
          this.purchaseList = [];
          this.returnList = [];
        } else if (this.orderType == 1) {
          this.orderList = [];
          this.returnList = [];
        } else {
          this.orderList = [];
          this.purchaseList = [];
        }
      } else {
        this.orderList = [];
        this.purchaseList = [];
        this.returnList = [];
      }
      if (this.orderList.length <= 0) {
        // do NOT show finish text when no data in list
        this.finishedText = '';
      } else {
        this.finishedText = '没有更多了';
      }
    },
    processKey() {
      if(this.subBillNo === '') {
        this.$toast("订单号不能为空！");
        return;
      }
      this.orderData = { page: 1, row: 10, rows: 10 };
      this.orderList = [];
      this.purchaseList = [];
      this.returnList = [];
      if (document.querySelector(".commerce-over-box").scrollTop != 0) {
        console.log("reset scroll top value before research." + document.querySelector(".commerce-over-box").scrollTop);
        document.querySelector(".commerce-over-box").scrollTop = 0;
      }
      if (this.orderType == 0) {
        this.getOrderList();
      } else if (this.orderType == 1) {
        this.getPurchaseList();
      } else {
        this.getReturnList();
      }
    },
    cancelSearch() {
      this.subBillNo = "";
    },
    //获取订单列表
    getOrderList() {
      delete this.orderData.recAreaCode;// there is not area code for order list request.
      this.orderData.subBillNo = this.subBillNo;
      this.orderLoading = true;
      getOrderByKeyService(this.orderData)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            this.orderList = this.orderList.concat(result.data);
            //最后一页，结束分页加载
            if (result.pages <= this.orderData.page || result.pages == 0) {
              this.orderFinished = true;
            } else {
              this.orderFinished = false;
              this.orderData.page++;
            }
          } else {
            result.msg
              ? this.$toast(result.msg)
              : this.$toast("发送失败，请重试！");
            //加载失败后，关闭加载等待的文字
            this.orderFinished = true;
          }
          this.orderLoading = false;
          if (this.orderList.length <= 0) {
            // do NOT show finish text when no data in list
            this.finishedText = '';
          } else {
            this.finishedText = '没有更多了';
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
          this.orderLoading = false;
          this.orderFinished = true;
          if (this.orderList.length <= 0) {
            // do NOT show finish text when no data in list
            this.finishedText = '';
          } else {
            this.finishedText = '没有更多了';
          }
        });
    },
    getPurchaseList() {
      let custNews = (store.get("custNews") && store.get("custNews").AREA_CODE) ?
              store.get("custNews") : storage.getStorage("custNews");
      if (this.userInfo.AREA_CODE == "" || this.userInfo.AREA_CODE == undefined) {
        log.i("area code empty, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
      }
      if (this.userInfo.AREA_CODE != custNews.AREA_CODE) {
        log.i("area code changed, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
      }
      this.orderData.recAreaCode = this.userInfo.AREA_CODE;
      this.orderData.billNo = this.subBillNo;
      this.purchaseLoading = true;
      getPurchaseListService(this.orderData)
        .then(result => {
          if (result.resultCode == "0") {
            this.purchaseList = this.purchaseList.concat(result.data)
            if (result.pages <= this.orderData.page || result.pages == 0) {
              this.purchaseFinished = true;
            } else {
              this.purchaseFinished = false;
              this.orderData.page++;
            }
          } else {
            //加载失败，给出错误提示
            result.msg
              ? this.$toast(result.msg)
              : this.$toast("发送失败，请重试！");
            this.purchaseFinished = true;
          }
          this.purchaseLoading = false;
          if (this.purchaseList.length <= 0) {
            // do NOT show finish text when no data in list
            this.finishedText = '';
          } else {
            this.finishedText = '没有更多了';
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
          this.purchaseFinished = true;
          this.purchaseLoading = false;
          if (this.purchaseList.length <= 0) {
            // do NOT show finish text when no data in list
            this.finishedText = '';
          } else {
            this.finishedText = '没有更多了';
          }
        });
    },
    //获取退货列表方法
    getReturnList() {
      delete this.orderData.recAreaCode;// there is not area code for return list request.
      this.orderData.returnOrderNo = this.subBillNo;
      this.returnLoading = true;
      getReturnOrderByKeyService(this.orderData)
        .then(result => {
          if (result.resultCode == "0") {
            this.returnList = this.returnList.concat(result.data)
            //最后一页，结束分页加载
            if (result.pages <= this.orderData.page || result.pages == 0) {
              this.returnFinished = true;
            } else {
              this.returnFinished = false;
              this.orderData.page++;
            }
          } else {
            //加载失败，给出错误提示
            result.msg
              ? this.$toast(result.msg)
              : this.$toast("发送失败，请重试！");
            this.returnFinished = true;
          }
          this.returnLoading = false;
          if (this.returnList.length <= 0) {
            // do NOT show finish text when no data in list
            this.finishedText = '';
          } else {
            this.finishedText = '没有更多了';
          }
        })
        .catch(error => {
          //加载失败后，关闭加载动画
          this.$toast("网络异常，请稍后重试！");
          this.returnFinished = true;
          this.returnLoading = false;
          if (this.returnList.length <= 0) {
            // do NOT show finish text when no data in list
            this.finishedText = '';
          } else {
            this.finishedText = '没有更多了';
          }
        });
    },
    loadMore() {
      setTimeout(() => {
        if(this.subBillNo === '') {
          this.$toast("订单号不能为空！");
          return;
        }
        if (this.orderType == 0) {
          this.getOrderList();
        } else if (this.orderType == 1) {
          this.getPurchaseList();
        } else {
          this.getReturnList();
        }
      }, 500);
    },
    //订单收货按钮事件
    received(orderInfo, index) {
      var data = {
        subBillNo: orderInfo.subBillNo,
        signerType: "03",
        signerCode: this.userInfo.USER_CODE
      };
      setOrderReceived(data)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            store.get("dealFunction")(orderInfo.subBillNo);
            this.orderList[index].billStatus = "已完成";
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    //到货通知按钮事件
    goodArrive(orderInfo) {
      if (this.dadaExpressSurported && orderInfo.noticeTimes && orderInfo.noticeTimes >= 1) {
        console.log("goods already arrived, need not request, ignore.");
        return;
      }
      var data = {
        subBillNo: orderInfo.subBillNo,
        userPhone: this.userInfo.PHONE
      };
      sendGoodsArriveMsg(data)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            this.$toast("发送成功");
            orderInfo.noticeTimes = 1;
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    adoptReturn(orderInfo, index) {
      this.setReturnStatus(orderInfo.returnBillNo, 1, index);
    },
    rejectReturn(orderInfo, index) {
      this.setReturnStatus(orderInfo.returnBillNo, 0, index);
    },
    //退货订单审批
    setReturnStatus(returnBillNo, auditResult, index) {
      var requestData = {
        returnBillNo: returnBillNo,
        auditResult: auditResult
      };
      setReturnStatusService(requestData)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            store.get("returnDealFunction")(returnBillNo, auditResult ? "已完成" : "已驳回");
            this.returnList[index].billStatus = auditResult ? "已完成" : "已驳回";
            this.$toast("审批成功");
          } else {
            this.$toast("审批失败，请重试！");
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    gotoCommerceDetail(orderInfo) {
      store.set("searchKey", this.subBillNo);
      console.log(orderInfo);
      this.$router.push({
        path: "/commerceOrderDetail",
        query: {
          orderInfo: orderInfo
        }
      });
    },
    //进入退货详情
    gotoReturnDetail(order) {
      store.set("searchKey", this.subBillNo);
      store.set("returnDealFunction", this.changeDataFromList);
      this.$router.push({
        path: "/commerceReturnDetail",
        query: {
          orderInfo: order
        }
      });
    },
    onClickLeft() {
      this.subBillNo = '';
      this.$router.back();
    },
    getDadaExpressInfo(orderInfo) {
      this.$router.push({ name: "mapForExpress", params: { subBillNo: orderInfo.subBillNo } });
        // getDadaOrderTrack(orderInfo.subBillNo)
        //   .then(result => {
        //     console.log("the track result " + result);
        //     // if is dada express
        //     // show dada track url in new page
        //     this.$toast("the track result " + JSON.stringify(result));
        //     let url = 'https://www.baidu.com';
        //     cordova.InAppBrowser.open(
        //             url,
        //     );
        //   })
        //   .catch(err => {
        //     console.log("get dada order info exception: " + err);
        //     this.$toast(err);
        //   })

          // for debug
          // let url = 'https://www.baidu.com';
          // cordova.InAppBrowser.open(
          //         url,
          // );
    },
  }
};
</script>

<style>
.commerceSearch {
  background-color: #f2f2f2;
  height: 100%;
  box-sizing: border-box;
  padding-top: 100px;
}
.commerceSearch .swipe-div {
  margin-top: 10px;
}
.commerceSearch .commerce-over-box {
  position: absolute;
  top: 88px;
  bottom: 0;
  overflow: auto;
  width: 100%;
}
.commerceSearch .commerce-over-box .noMessagePanel{
  width: 100%;
  padding-top: 6rem;
}
.commerceSearch .commerce-over-box .noMessagePanel .noMessage {
  margin: 1rem auto;
  text-align: center;
  color: #ddd;
  font-size: 13px;
}
.commerceSearch .commerce-over-box .noMessagePanel .noMessageImg{
  display:block;
  width:2rem;
  margin: 0 auto;
}
/* 列表搜索 */
.commerceSearch .commerceSearchPanel {
  background: #f7f7f7;
  border-radius: 2px;
  margin: 0 10px 0 10%;
  display: flex;
  margin-left: 50px;
}

.commerceSearch .commerceSearchPanel .CommerceSearchImg {
  width: 14px;
  margin: 10px;
}

.commerceSearch .commerceSearchPanel .CommerceSearchInput {
  width: 80%;
  border: none;
  padding: 0;
  background: #f7f7f7;
  font-size: 14px;
}
/* 订单列表 START */
.commerceOrderList .card {
  padding-top: 1px;
  padding-bottom: 1px;
  box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
  border-radius: 5px;
  overflow: hidden;
  margin: 10px 10px;
  background-color: #fff;
}

/* 店铺名+订单号 */
.commerceOrderList .orderTitlePanel {
  height: 50px;
  line-height: 50px;
  padding: 0 15px;
  position: relative;
}

.commerceOrderList .orderTitlePanel .orderTitleStore {
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.04px;
  font-weight: bold;
